body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Import Google Fonts */


/* Global */
body {
  background-color: #f7f7f7;
  min-height: 100vh;
}

.app {
  position: relative;
  text-align: center;
  min-height: 100vh;
  padding-bottom: 40px;
}

footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 10px;
  margin: 10px auto 0;
  background-color: #1b1c1d;
  box-shadow: 0 0 5px black;
}

footer a {
  color: white;
}

header {
  background-color: #222;
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 0 5px white;
}

.toolStatus {
  position: absolute;
  font-size: 0.7rem !important;
  top: -3px;
  left: 3px;
}

.componentAppender {
  padding: 20px;
}

.componentAppender button {
  margin: 3px !important;
}

.buttonContainer {
  margin: 0 auto;
}

.relative {
  position: relative;
}

.flex {
  /* display: flex;
  flex-wrap: wrap;
  justify-content: center; */
  display: grid;
  grid-template-columns: repeat(5,1fr);
  grid-auto-flow: dense;
  max-width: 100%;
  padding: 10px;
  grid-gap: 6px;
}

table {
  overflow-x: scroll !important;
  overflow-y: hidden !important;
  display: block;
}

td {
  min-width: 80px;
  padding: 0 !important;
}

input[type="number"],
tfoot th {
  -webkit-appearance: none !important;
  -moz-appearance: textfield !important;
  padding: 0 !important;
  height: 3rem;
  text-align: center !important;
  font-size: 1.2rem;
  background-color: transparent !important;
}

tr {
  text-align: center;
}

.ui.inverted.definition.table tfoot:not(.full-width) th:first-child, .ui.inverted.definition.table thead:not(.full-width) th:first-child {
  background-color: transparent;
  box-shadow: none;
}

.ui.inverted.input>input {
  color: whitesmoke;
}

th input,
.ui[class*="corner labeled"]:not([class*="left corner labeled"]).labeled.input > input {
  text-align: center !important;
  padding: 0 !important;
  height: 3rem;
  font: inherit !important;
  font-weight: 800 !important;
  background-color: inherit !important;
  border-radius: none !important;
}

input[type="color"] {
  padding: 0 !important;
}

tr th {
  text-align: left;
  padding: 0 !important
}

tfoot button.ui.fluid.button {
  padding: 0;
  height: 3rem;
}


/* Tool Container */
.toolContainer {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 200px;
  background-color: #ffffff;
  box-shadow: 0 1px 2px #ccc;
  padding: 15px;
  border-radius: 6px;
  margin: 2px;
  /* min-width: 300px; */
  /* max-width: 300px; */
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  grid-column-end: span 1;
}

.scoreContainer {
  /* width: 600px; */
  /* max-width: 600px; */
  grid-column-end: span 2;
}

/* For Timers on Larger Screens */

.mini {
  font-size: 0.9rem;
  line-height: normal;
  line-height: initial;
}

.ui.buttons {
  width: 100%;
}

.ui.buttons .button {
  flex: 1 1 auto;
  font-size: 0.9rem;
}

.dice {
  font-size: 3rem;
  display: block;
  margin: 0 5px 5px;
}

.diceButtons .button {
  padding: 8px 5px;
}

.diceButtons .dice {
  font-size: 2.5rem;
}

.diceContainer {
  min-height: 50px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 9px;
}

/* Add User Object Button Icon
Add Score Type Button Icon */

/* Delete Butotns */
.delete {
  position: absolute;
  /* color: #011627; */
  background: none;
  border: none;
  top: 8px;
  right: 8px;
  /* z-index: 10; */
  cursor: pointer;
  width: auto;
  width: initial;
  padding: 0 5px;
}

h1 {
  font-size: 1.8rem !important;
  margin: 0 auto !important;
}

.darkToggle {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: absolute;
  right: 5px;
}

.delete:hover {
  color: grey;
}

canvas {
  width: 100%;
}

/* Timer/Countdown Tool Timer Display */
.timeDisplay {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: flex-end;
  margin: 0 auto 20px;
  padding: 5px 7px;
  border-radius: 5px;
  font-family: 'Inconsolata', monospace;
  font-size: 1.6rem;
  font-weight: bold;
  letter-spacing: 4px;
  line-height: inherit;
  background-color: #011627;
  color: #f6f7f8;
}

.timeDisplay.darkMode {
  background-color: #2b2b2b;
  /* color: #f6f7f8; */
}

.darkToggleSwitch {
  margin: 0 4px 0 1px;
}

.ui.toggle.checkbox input:checked ~ .box::before, .ui.toggle.checkbox input:focus:checked ~ label::before,
.ui.toggle.checkbox input:checked ~ .box::before, .ui.toggle.checkbox input:checked ~ label::before {
  background-color: grey !important
}

.ui.toggle.checkbox .box::before, .ui.toggle.checkbox label::before,
.ui.toggle.checkbox .box::before, .ui.toggle.checkbox label:hover::before,
.ui.toggle.checkbox input:focus ~ .box::before, .ui.toggle.checkbox input:focus ~ label::before {
  background-color: lightblue;
}

.darkMode,
.ui.inverted.header,
.ui.inverted.button {
  color: rgba(255, 255, 255, 0.8)
}

.ui.inverted.button {
  box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.8) inset !important;
}

.ui.inverted.button:hover {
  background-color: rgba(255, 255, 255, 0.8)
}

.darkMode.app {
  background-color: #1a1b1c;
}

.darkMode.toolContainer {
  background-color: #222;
  box-shadow: 0 0 3px #ccc;
}


/* Media Queries */
@media (min-width: 1480px){
  .flex {
    grid-template-columns: repeat(6,1fr);
  }
}

@media (max-width: 1280px) {
  * {
    font-size: 14px;
  }

  .flex {
    grid-template-columns: repeat(4,1fr);
  }

  .toolContainer {
    padding: 10px;
  }
}

@media (max-width: 980px) {
  h1 {
    font-size: 1.4rem !important;
    padding: 10px 0 !important;
  }
  
  .flex {
    grid-template-columns: repeat(3,1fr);
  }

  header {
    padding: 0;
  }

  .toolContainer, .scoreContainer {
    min-width: none;
    /* max-width: none; */
    /* width: 100%; */
    /* margin: 0.5rem 2rem; */
    padding: 1.6rem 1.4rem;
  }

  table {
    display: block;
  }

  .buttonContainer {
    width: 100%;
  }

}

@media (max-width: 800px) {
  .timeDisplay {
    width: 100%;
  }

  td {
    min-width: none;
    width: auto;
  }

  .flex {
    grid-template-columns: repeat(2,1fr);
  }

}


@media (max-width: 540px) {
  body {
    font-size: 12px;
  }
  
  .darkToggle {
    position: relative;
  }



  .toolContainer {
    grid-column-end: span 2;
  }


}


